<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-employee-kpi',
  data () {
    return {
      config: {
        title: 'KPI Tenaga Kerja',
        model_api: 'employee-kpi',
        getter: 'employee-kpi',
        setter: 'employee-kpi',
        pk_field: null,
        permission: {
          create: 'create-employee-kpi',
          read: 'view-employee-kpi',
          update: 'update-employee-kpi',
          delete: 'delete-employee-kpi'
        },
        slave: [],
        fields: [
          { id: 'employee_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'periode_date', label: 'Tanggal Penilaian', methods: { list: { transform: 'longDate' }, detail: { transform: 'longDate' }, create: { type: 'date' }, update: { type: 'date' }, filter: false } },
          { id: 'kpi_value', label: 'Nilai KPI' },
          // {
          //   id: 'employee_id',
          //   label: 'Nama Tenaga Kerja',
          //   methods: {
          //     create: {
          //       setter: 'employees',
          //       getter: 'employees',
          //       type: 'lookup-radio',
          //       option: {
          //         list_pointer: {
          //           label: 'fullname', code: 'id', display: ['nip', 'fullname']
          //         }
          //       }
          //     },
          //     update: {
          //       setter: 'employees',
          //       getter: 'employees',
          //       type: 'lookup-radio',
          //       option: {
          //         list_pointer: {
          //           label: 'fullname', code: 'id', display: ['nip', 'fullname']
          //         }
          //       }
          //     },
          //     filter: {
          //       setter: 'employees',
          //       getter: 'employees',
          //       type: 'lookup-radio',
          //       option: {
          //         list_pointer: {
          //           label: 'fullname', code: 'id', display: ['nip', 'fullname']
          //         }
          //       }
          //     }
          //   }
          // },
          {
            id: 'file_document',
            label: 'File Dokumen',
            methods: {
              list: false,
              detail: {
                type: 'file',
                view_data: 'file_document.url'
              },
              create: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }]
              },
              update: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }],
                view_data: 'file_document'
              }
            }
          }
        ]
      }
    }
  }
}
</script>



